.home-half {
    padding-bottom: 130px !important;
    padding-top: 200px !important;
}

.bg-overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    right: 0;
    bottom: 0;
    left: 0;
    top: 0;
}

.bg-image {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    opacity: .55;
    overflow: hidden;
}

.bg-image img {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.text-white {
    color: black !important;
}

.home-small-title {
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 7px;
}

.home-title {
    font-size: 46px;
    line-height: 64px;
    font-weight: 300;
}

.home-desc {
    color: rgba(0,0,0,.7);
    font-size: 15px;
    max-width: 600px;
}

.navbar-custom.navbar-white {
    background-color: #fff;
    padding: 10px 0;
    box-shadow: 0 10px 33px rgba(0,0,0,.1);
}

.navbar-custom {
    padding: 20px 0;
    width: 100%;
    border-radius: 0;
    z-index: 999;
    margin-bottom: 0;
    transition: all .5s ease-in-out;
}

.fixed-top {
    top: 0;
}

.navbar-custom .navbar-nav li a {
    color: hsla(0,0%,100%,.6);
    font-size: 15px;
    background-color: transparent!important;
    padding: 15px 0;
    margin: 0 10px;
    transition: all .4s;
}

.navbar-custom.navbar-white .navbar-brand, .navbar-custom.navbar-white .navbar-nav li a, .navbar-white .navbar-toggle {
    color: #101016!important;
}

.navbar-custom .navbar-brand {
    text-transform: uppercase;
    color: #fff!important;
    font-weight: 700;
    letter-spacing: 2px;
    height: auto;
}

.navbar-brand {
    display: inline-block;
    padding-top: .3125rem;
    padding-bottom: .3125rem;
    margin-right: 1rem;
    font-size: 1.25rem;
    line-height: inherit;
    white-space: nowrap;
}

a, a:focus, a:hover, button {
    text-decoration: none!important;
    outline: none!important;
}

a {
    color: #007bff;
    text-decoration: none;
    background-color: transparent;
}

#navbarNav {
    justify-content: flex-end;
}

.navbar-custom.navbar-white .navbar-nav li a:active, .navbar-custom.navbar-white .navbar-nav li a:focus, .navbar-custom.navbar-white .navbar-nav li a:hover, .social-icon:hover i, .text-custom {
    color: #4d6eb3!important;
}

